let httpDef = require('httpDef').httpDef;

let {Http, IHttpMethod } = require('Http');

let EventManager = require('EventManager');

let Language = ["brasil","columbia","spanish"]

let ConfirmPopup = require('ConfirmPopup');



cc.Class({
    extends: cc.Component,

    properties: {
          
        btn_rule:  {
            default: null,
            type: cc.Button
        },

        btn_play: {
            type: cc.Button,
            default: null,
        },


        audioSource: cc.AudioSource,

        language: {
            type: cc.Enum({
                Brasil: 0,
                Columbia: 1,
                Spanish: 2,
            }),
            default: 0
        },

        wheelSprite: cc.Sprite, // 
        _totalSlices: 54, // 
        _targetSliceIndex: 0, // 0  54
        _rotateDuration: 3, // 2


        layout_record: {
            type: cc.Node,
            default: null,
        },

        _maxRecords: 10,

        btn_min: {
            type: cc.Button,
            default: null,
        },
        btn_max: {
            type: cc.Button,
            default: null,
        },
        btn_half: {
            type: cc.Button,
            default: null,
        },
        btn_double: {
            type: cc.Button,
            default: null,
        },

        txt_bet: {
            type: cc.Label,
            default: null,
        }, 

        txt_score:{
            default: null,
            type: cc.Label
        },

        btn_play:{
            default: null,
            type: cc.Button
        },

        txt_count: {
            type: cc.Label,
            default: null,
        }, 

        options_toggle:{
            default: [],
            type: [cc.Button]
        },

        txt_time : {
            type: cc.Label,
            default: null,
        },
        
        popupNode : {
            type: cc.Node,
            default: null,
        },

        jiesuanLayer: {
            type: cc.Node,
            default: null,
        },

        manual_btn: {
            type: cc.Button,
            default: null,
        },
        auto_btn: {
            type: cc.Button,
            default: null,
        },
        autoOk_btn: {
            type: cc.Button,
            default: null,
        },
         
        auto_bg2: {
            type: cc.Node,
            default: null,
        },

        mask: {
            type: cc.Node,
            default: null,
        },

        editbox_number_of_bets: {
            type: cc.EditBox,
            default: null,
        },
        editbox_stop_on_profit: {
            type: cc.EditBox,
            default: null,
        },
        editbox_stop_on_loss: {
            type: cc.EditBox,
            default: null,
        },
        editbox_max_bet_amount: {
            type: cc.EditBox,
            default: null,
        },
        editbox_on_win: {
            type: cc.EditBox,
            default: null,
        },
        editbox_on_loss: {
            type: cc.EditBox,
            default: null,
        },

        toggle_on_win: {
            type: cc.Toggle,
            default: null,
        },
        toggle_on_loss: {
            type: cc.Toggle,
            default: null,
        },

        ruleLayer: {
            type: cc.Node,
            default: null,
        },


    },


    onLoad () {


        this.initData()
        
        //Url
        let account = "";
        let token = "";
        let address = ""
        let _language = ""
        var parmStr = window.location.href.split('?')[1];
        if (parmStr) {
            var parms = parmStr.split('&');
            parms.forEach(element => {
                var parm = element.split('=');
                if (parm[0] == "member_account") {
                    account = parm[1];
                }
                else if (parm[0] == "token") {
                    token = parm[1];
                    this.token = token
                }
                else if (parm[0] == "svr") {
                    address = "https://" + parm[1];
                }else if (parm[0] == "language") {
                    _language = parm[1];
                }
            });
        }

        if (CC_DEBUG) {
            this.address = "http://192.168.1.36:8080"
        }
        else {
            if(address === ""){
                this.address = "https://game.yotuwin.com"
            }else{
                this.address = address
            }

            if(_language === "PT" ){        //
                this.language = 0
                this.min_bet = 1
                this.max_bet = 128
            }else if(_language === "es"){ //
                this.language = 2
                this.min_bet = 100
                this.max_bet = 12800
            }
        }

        

        cc.resources.preloadDir("game",function(){
            var infos = cc.resources.getDirWithPath('game', cc.SpriteFrame);
            let paths = infos.map(function (info) {
                return info.path;
            });
        }.bind(this));

        this.resList
        cc.resources.loadDir("game", function(err, assets){
 
            let list = cc.resources.getDirWithPath("game")
            let uuidDict = {}
         
            for(let item of list) {
                uuidDict[item.uuid] = item
            }
         
            let resList = {}
            
            for(let asset of assets) {
                let path = uuidDict[asset["_uuid"]].path
                if(!resList[path]) {
                    resList[path] = []
                }
                resList[path].push(asset)
            }
            this.resList = resList
         
        }.bind(this))

        this.prefebList
        cc.resources.loadDir("prefebs", function(err, assets){
 
            let list = cc.resources.getDirWithPath("prefebs")
            let uuidDict = {}
         
            for(let item of list) {
                uuidDict[item.uuid] = item
            }
         
            let resList = {}
            
            for(let asset of assets) {
                let path = uuidDict[asset["_uuid"]].path
                if(!resList[path]) {
                    resList[path] = []
                }
                resList[path].push(asset)
            }
            this.prefebList = resList

            httpDef.ReqDoubleQueryEnterRoom.token = this.token
            httpDef.ReqDoubleQueryEnterRoom.language = "en"
    
            Http.request(this.address + "/double/enterroom?token=" + this.token,httpDef.ReqDoubleQueryEnterRoom,function(data) {
                //cc.log(data);
                try {
                    const parsedData = JSON.parse(data);
                    this.gold = parsedData.data.gold
                    this.time = parsedData.data.remaintime
                    //this.txt_time.string = "Comegando em " + parsedData.data.remaintime + "s"
                    this.records = parsedData.data.recordlist
                    this.updateRecordDisplay()
                    this.startTimer()
                    this.isShowTime = true
                    this.play_btn_state = true
                    this.txt_score.string =  "R$   " + this.gold
                } catch (error) {
                    console.error(' JSON :', error);
                }
                
                
            }.bind(this),IHttpMethod.POST,false)
         
        }.bind(this))






        this.btn_play.node.on('click', this.playButtonClickEvent, this)
        this.btn_min.node.on('click', this.betButtonClickEvent, this)
        this.btn_max.node.on('click', this.betButtonClickEvent, this)
        this.btn_half.node.on('click', this.betButtonClickEvent, this)
        this.btn_double.node.on('click', this.betButtonClickEvent, this)

        this.ConfirmPopup = this.popupNode.getComponent("ConfirmPopup")

        
        for(var i = 0; i < this.options_toggle.length; i++){
            
            this.options_toggle[i].node.on('click', this.optionsToggleButtonClickEvent, this)
        }

        this.manual_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        this.auto_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        this.autoOk_btn.node.on('click', this.switchAutoButtonClickEvent, this)
        this.editbox_number_of_bets.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_stop_on_profit.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_stop_on_loss.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_max_bet_amount.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_on_win.node.on('editing-did-ended', this.EditboxEvent, this);
        this.editbox_on_loss.node.on('editing-did-ended', this.EditboxEvent, this);
        
        this.toggle_on_win.node.on('click', this.resetIncreaseEvent, this);
        this.toggle_on_loss.node.on('click', this.resetIncreaseEvent, this);
        this.btn_rule.node.on('click', this.ruleButtonClickEvent, this)
    },



    initData(){

        this.min_bet = 100
        this.max_bet = 12800

        this.bet = this.min_bet

        this.token = ""

        this.records = []; // 

        this.time = 0
        this.gold = 0       //
        this.isShowTime = false
        this.isbet = false  //

        this.colorchoose = 0    //


        this.totalwin = 0   //

        this.balancegold = 0//


        this.isAuto = false
        this.numberofbet = 0
        this.stoponprofit = 99999999        //
        this.stoponloss = 99999999          //
        this.maxbetamount = 99999999        //
        this.onwinincrease = 0              //
        this.onlossincrease = 0             //
        this.iswinresetIncrease = false
        this.islossresetIncrease = false
        

        this.play_btn_state = false
    },

    resetData(){

        this.totalwin = 0

        this.isAuto = false

        this.numberofbet = 0
        this.stoponprofit = 99999999
        this.stoponloss = 99999999
        this.maxbetamount = 99999999
        this.onwinincrease = 0
        this.onlossincrease = 0
        this.iswinresetIncrease = false
        this.islossresetIncrease = false
        

    },

    start () {

        //
        EventManager.default.emit('lang-change', Language[this.language]);
    },

    update (dt) {

        //
        EventManager.default.emit('lang-change', Language[this.language]);

        // this.txt_score.string =  "R$   " + this.gold

        this.btn_play.interactable = this.play_btn_state
        for(var i = 0; i < this.options_toggle.length; i++){
            
            this.options_toggle[i].interactable = this.play_btn_state
        }

        this.btn_min.interactable = this.play_btn_state
        this.btn_max.interactable = this.play_btn_state
        this.btn_half.interactable = this.play_btn_state
        this.btn_double.interactable = this.play_btn_state

        
        if(this.language == 0 || this.language == 1){
            if(this.bet > this.gold){
                this.txt_bet.string = "R$   " + this.gold
            }else{
                this.txt_bet.string = "R$   " + this.bet
            }
            this.txt_score.string =  "R$   " + this.gold
        }else if(this.language == 2 ){
            if(this.bet > this.gold){
                this.txt_bet.string = "$   " + this.gold
            }else{
                this.txt_bet.string = "$   " + this.bet
            }
            this.txt_score.string =  "$   " + this.gold
        }
        


        this.iswinresetIncrease = this.toggle_on_win.isChecked
        this.islossresetIncrease = this.toggle_on_loss.isChecked

        if (this.numberofbet > 0) {
            this.txt_count.string = this.numberofbet
            this.txt_count.node.active = true
        }else{
            this.txt_count.string = ""
            this.txt_count.node.active = false
        }

        if(this.isShowTime){
            if(this.language == 0 || this.language == 1){
                this.txt_time.string = "Comegando em " + this.time + "s"
            }else if(this.language == 2 ){
                this.txt_time.string = "Empezando en " + this.time + "s"
            }
            
        }else{
            this.txt_time.string = ""
        }

        
    },

    // 
    startTimer() {
        //  updateFunction 
        this.timerId = setInterval(this.gameQuery.bind(this), 1000);
    },

    //
    gameQuery(){


        httpDef.ReqDoubleQuery.token = this.token
        httpDef.ReqDoubleQuery.language = "en"
    
        Http.request(this.address + "/double/query?token=" + this.token,httpDef.ReqDoubleQuery,function(data) {
            //cc.log(data);
            try {
                const parsedData = JSON.parse(data);
                
                this.gold = parsedData.data.gold
                
                this.time = parsedData.data.remaintime
                this.records = parsedData.data.recordlist       //
                this._targetSliceIndex = parsedData.data.rewardid //
                this.balancegold = Number(parsedData.data.balancegold )

                

                if(this._targetSliceIndex >= 0){
                    this.isShowTime = false
                    this.play_btn_state = false
                    this.startRotateToTarget(this.balancegold)
                }

            } catch (error) {
                console.error(' JSON :', error);
            }
            
            
        }.bind(this),IHttpMethod.POST,false)
        
    },    

    

    //
    playButtonClickEvent(){

        this.startGame(0)
        
    }, 


    startGame(balancegold){

        httpDef.ReqDoubleQueryBet.token = this.token
        httpDef.ReqDoubleQueryBet.language = "en"



        if(this.isAuto === true){
            //
            if (balancegold > 0){
                
                this.bet = Help.truncateToTwoDecimalPlacesAndHideZero(this.bet + (this.bet * ((this.iswinresetIncrease === true) ? this.onwinincrease : 0)/100),2)

            }else if(balancegold < 0){
                this.bet = Help.truncateToTwoDecimalPlacesAndHideZero(this.bet + (this.bet * ((this.islossresetIncrease === true) ? this.onlossincrease : 0)/100),2)
            }

            this.bet = (this.bet >= this.maxbetamount) ? this.maxbetamount : this.bet
            
            var num2 = this.bet.toFixed(3)
            this.bet = Number(num2.substring(0,num2.indexOf('.') + 3))
        }

        
        httpDef.ReqDoubleQueryBet.bet = this.bet
        httpDef.ReqDoubleQueryBet.colorchoose = this.colorchoose


        this.play_btn_state = false

        Http.request(this.address + "/double/bet",httpDef.ReqDoubleQueryBet,function(data) {
            //cc.log(data);
            try {

                const parsedData = JSON.parse(data);
                if(parsedData.code === -1){
                    // this.dialog("",parsedData.msg)
                    this.dialog("","Por favor, saia da sala e entre novamente no jogo")
                    
                    return
                }

                this.isbet = true //

                if(this.isAuto === true){
                    this.numberofbet = this.numberofbet - 1
                }


            } catch (error) {
                console.error(' JSON :', error);
            }
        }.bind(this),IHttpMethod.POST,false)
    },


    //
    startRotateToTarget(balancegold) {
        // 
        let targetAngle = -this._targetSliceIndex * (360 / this._totalSlices);
        let currentAngle = this.wheelSprite.node.rotation; // 

        let deltaAngle = ((targetAngle - currentAngle) % 360) + 720 ; // 

        if(deltaAngle<720){
            deltaAngle = deltaAngle + 360
        }

        // 
        let fastRotate = cc.rotateBy(this._rotateDuration, deltaAngle); // 70%

        // 
        let rotateAction = cc.sequence(fastRotate,cc.callFunc(function(){
            // cc.log(this.wheelSprite.node.rotation)
            // cc.log(this._targetSliceIndex)
            this.isShowTime = true
            this.play_btn_state = true
            this.updateRecordDisplay()

            //
            if(this.isbet == false){
                return
            }

            this.totalwin = this.totalwin - this.bet + balancegold
            cc.log("bet : " + this.bet)
            cc.log("balancegold : " + balancegold)
            cc.log("totalwin : " + this.totalwin)
            cc.log("stoponprofit : " + this.stoponprofit)
            cc.log("stoponloss : " + this.stoponloss)
            let jiesuanLayerOptions = {
                num :(balancegold === 0) ? -this.bet : balancegold - this.bet,
                parent:this
            }
            this.jiesuanLayer.getComponent("jiesuanLayer").show(jiesuanLayerOptions)  
            this.txt_score.string =  "R$   " + this.gold
            this.isbet = false
            setTimeout(function(){
                this.jiesuanLayer.getComponent("jiesuanLayer").hide()
                if (this.isAuto == true){
                    if(Number(this.numberofbet)>0 && this.totalwin <= this.stoponprofit && this.totalwin >= -this.stoponloss ){
                        this.startGame((balancegold === 0) ? -this.bet : balancegold)
                    }else{
                        this.manual_btn.isChecked = true
                        this.resetAuto()
                    }
                    
                }
            }.bind(this),1000)

        }.bind(this)) );

        fastRotate.easing(cc.easeInOut(this._rotateDuration));

        this.wheelSprite.node.runAction(rotateAction);
   
    },

     //
     onLotteryResult(result) {
        // 
        this.records.push(result);

        // 
        if (this.records.length > this._maxRecords) {
            this.records.shift();
        }

        //cc.log(this.records)
        //  ScrollView  Label
        this.updateRecordDisplay();
    },

    updateRecordDisplay() {
        //  ScrollView 
        this.layout_record.removeAllChildren();

        //  Label  ScrollView
        for (let i = 0; i < this.records.length; i++) {
            const record = this.records[i];
            if(record != -1){
                //  
                const Prefeb = cc.instantiate(this.getPrefeb("prefebs/t" + record));

                this.layout_record.addChild(Prefeb);
            }

        }
    },


    //
    ruleButtonClickEvent(){
        this.ruleLayer.getComponent("ruleLayer").show({parent:this})  
    },

    //music
    musicButtonClickEvent(event){
        
        

    },

    //
    switchAutoButtonClickEvent(event){
        if(event.node.name === "manual_btn"){
            this.resetAuto()
        }else if (event.node.name === "auto_btn"){
            this.isAuto = false
            this.auto_bg2.active = true
            this.mask.active = true
        }else if (event.node.name === "autoOk_btn"){


            if(this.numberofbet === 0){
                if(this.language == 0 || this.language == 1){
                    this.dialog("","nmero de apostas no pode estar vazio!")
                }else if(this.language == 2 ){
                    this.dialog("","el nmero de apuestas no puede estar vaco")
                }
                
                return
            }


            if(this.iswinresetIncrease === true || this.islossresetIncrease === true){
                if(Number(this.editbox_max_bet_amount.string) === 0 || this.editbox_max_bet_amount.string === ""){
                    if(this.language == 0 || this.language == 1){
                        this.dialog("","Quando o aumento na ganha ou o aumentar a perda est ativado, o valor mx de aposta no pode servazio!")
                    }else if(this.language == 2 ){
                        this.dialog("","Cuando se activa el aumento de ganancias o el aumento de prdidas, el monto mximo de la apuesta no puede estar vaco!")
                    }
                    
                    return
                }

            }

            this.totalwin = 0
            //this.balancegold = 0
            this.isAuto = true
            this.auto_bg2.active = false
            this.mask.active = false

            if (this.play_btn_state === true){
                this.playButtonClickEvent()
            }else{
                this.myUpdate = function () {
                    
                    this.playButtonClickEvent()

                };

                this.scheduleOnce(this.myUpdate,3);
            }

        }
    },

    resetAuto(){
        this.resetData()

        this.editbox_number_of_bets.string = ""
        this.editbox_stop_on_profit.string = ""
        this.editbox_stop_on_loss.string = ""
        this.editbox_max_bet_amount.string = ""
        this.editbox_on_win.string = ""
        this.editbox_on_loss.string = ""
        this.isAuto = false
        this.auto_bg2.active = false
        this.mask.active = false
    },

    //
    EditboxEvent(event){
        
        if(event.node.name === "editbox_number_of_bets"){
            this.numberofbet = Number(event.string)
        }else if (event.node.name === "editbox_stop_on_profit"){
            this.stoponprofit = Number(event.string)
        }else if (event.node.name === "editbox_stop_on_loss"){
            this.stoponloss = Number(event.string)
        }else if (event.node.name === "editbox_max_bet_amount"){  
            if (Number(event.string)===0){return}
            this.maxbetamount = Number(event.string)
        }else if (event.node.name === "editbox_on_win"){ 
            if(Number(event.string) > 100){
                this.editbox_on_win.string = 0
            } 
            this.onwinincrease = Number(event.string)
        }else if (event.node.name === "editbox_on_loss"){  
            if(Number(event.string) > 100){
                this.editbox_on_loss.string = 0
            } 
            this.onlossincrease = Number(event.string)
        }

    },

    resetIncreaseEvent(event){

        
    },

    
    //
    optionsToggleButtonClickEvent(event){

        if(event.node.name === "toggle1"){
            this.colorchoose = 0
        }else if (event.node.name === "toggle2"){
            this.colorchoose = 1
        }else if (event.node.name === "toggle3"){
            this.colorchoose = 2
        }else if (event.node.name === "toggle4"){  
            this.colorchoose = 3
        }

    },

    //
    betButtonClickEvent(event){

        if(event.node.name === "btn_min"){
            this.bet = this.min_bet
        }else if (event.node.name === "btn_max"){
            
            if(this.gold > this.max_bet){
                this.bet = this.max_bet
            }else{
                this.bet = this.gold
            }
            
        }else if (event.node.name === "btn_half"){
            this.bet = (this.bet/2 < this.min_bet) ? this.min_bet : this.bet/2
        }else if (event.node.name === "btn_double"){  
            this.bet = (this.bet*2 > this.max_bet) ? this.max_bet : this.bet*2
        }

    },


    
    getRes(path, type) {
        let list = this.resList[path]
     
        if(!list) {
            return null
        }
     
        for(let item of list) {
            if(item instanceof(type)) {
                return item
            }
        }
     
        return null
    },

    getPrefeb(path) {
        let list = this.prefebList[path]
     
        if(!list) {
            return null
        }
     
        return list[0]

    },


    dialog(title,content,confirmCallback){
        const confirmOptions = {
            title: title,
            content: content,
            confirmCallback: confirmCallback
        };
        
        this.ConfirmPopup.show(confirmOptions)
    },
});
