import PopupBase from "../cocos-eazax-kit-master/components/popups/PopupBase";

const { ccclass, property } = cc._decorator;

@ccclass
export default class ruleLayer extends PopupBase<ruleOptions> {


    @property(cc.Sprite)
    private rule_sprite: cc.Sprite = null;
    @property(cc.Label)
    private rule_txt: cc.Label = null;
    @property(cc.Sprite)
    private hongdian: cc.Sprite = null;
    @property(cc.Sprite)
    private huidian: cc.Sprite = null;
    @property(cc.Node)
    private nextBtn: cc.Node = null;
    @property(cc.Node)
    private confirmBtn: cc.Node = null;
    // @property(cc.Node)
    // private rulebackBtn: cc.Node = null;

    @property(cc.Node)
    private min_txt: cc.Node = null;
    
    @property(cc.Node)
    private max_txt: cc.Node = null;

    @property(cc.Node)
    private min_num: cc.Node = null;
    
    @property(cc.Node)
    private max_num: cc.Node = null;

    @property(cc.Node)
    private checkBtn1: cc.Node = null;

    @property(cc.Node)
    private checkBtn2: cc.Node = null;
    @property(cc.Node)
    private confirmBtn1: cc.Node = null;

    private page = 1

    protected onLoad() {
        this.registerEvent();
    }

    protected onDestroy() {
        this.unregisterEvent();
    }

    private registerEvent() {
        this.nextBtn.on(cc.Node.EventType.TOUCH_END, this.onNextBtnClick, this);
        this.confirmBtn.on(cc.Node.EventType.TOUCH_END, this.onConfirmBtnClick, this);
        // this.rulebackBtn.on(cc.Node.EventType.TOUCH_END, this.onConfirmBtnClick, this);
        this.confirmBtn1.on(cc.Node.EventType.TOUCH_END, this.onConfirmBtnClick, this);

        this.checkBtn1.on(cc.Node.EventType.TOUCH_END, this.onCheckBtnClick, this);
        this.checkBtn2.on(cc.Node.EventType.TOUCH_END, this.onCheckBtnClick, this);
        
    }

    private unregisterEvent() {
        this.confirmBtn.targetOff(this);
    }

    protected init() {
        this.rule_sprite.spriteFrame = this.options.parent.getRes("game/rule01",cc.SpriteFrame)
        this.hongdian.spriteFrame = this.options.parent.getRes("game/hongdian",cc.SpriteFrame)
        this.huidian.spriteFrame = this.options.parent.getRes("game/huidian",cc.SpriteFrame)
        this.rule_txt.string = "" 
        this.nextBtn.active = true
        this.confirmBtn.active = false

        this.rule_sprite.node.active = true
            this.hongdian.node.active = true
            this.huidian.node.active = true
            this.rule_txt.node.active = true
            if(this.page == 1){
                this.nextBtn.active = true
                this.confirmBtn.active = false
            }else if(this.page == 2){
                this.nextBtn.active = false
                this.confirmBtn.active = true
            }


        this.checkBtn1.getComponent(cc.Toggle).isChecked = true
        this.confirmBtn1.active = false
        this.min_txt.active = false
        this.max_txt.active = false
        this.min_num.active = false
        this.max_num.active = false

        this.page = 1
    }

    protected updateDisplay(options: ruleOptions): void {


    }

    protected onNextBtnClick() {
        this.rule_sprite.spriteFrame = this.options.parent.getRes("game/rule02",cc.SpriteFrame)
        this.hongdian.spriteFrame = this.options.parent.getRes("game/huidian",cc.SpriteFrame)
        this.huidian.spriteFrame = this.options.parent.getRes("game/hongdian",cc.SpriteFrame)
        this.rule_txt.string = "" 
        this.nextBtn.active = false
        this.confirmBtn.active = true
        this.page = 2
    }

    protected onConfirmBtnClick() {
        this.page = 1
        this.hide();
    }

    protected onCheckBtnClick(event) {
        if(event.target.name == "toggle1"){
            this.rule_sprite.node.active = true
            this.hongdian.node.active = true
            this.huidian.node.active = true
            this.rule_txt.node.active = true
            if(this.page == 1){
                this.nextBtn.active = true
                this.confirmBtn.active = false
            }else if(this.page == 2){
                this.nextBtn.active = false
                this.confirmBtn.active = true
            }

            this.min_txt.active = false
            this.max_txt.active = false
            this.min_num.active = false
            this.max_num.active = false
            this.confirmBtn1.active = false
            
        }else if(event.target.name == "toggle2"){
            this.nextBtn.active = false
            this.confirmBtn.active = false
            this.rule_sprite.node.active = false
            this.hongdian.node.active = false
            this.huidian.node.active = false
            this.rule_txt.node.active = false

            this.min_txt.active = true
            this.max_txt.active = true
            this.min_num.active = true
            this.max_num.active = true
            this.confirmBtn1.active = true


        }
    }

}

/**  */
export interface ruleOptions {
    parent:any
}
