(function(scope){
    scope.Help =  {};

    //
    Help.preserveDecimal = function(number, precision) {
        const scale = Math.pow(10, precision);
        return (Math.floor(number * scale) / scale).toFixed(precision);
    }

    //
    Help.truncateToTwoDecimalPlacesAndHideZero = function(num,precision) {

        var num2 = num.toFixed(precision + 1)
        return Number(num2.substring(0,num2.indexOf('.') + precision + 1))

    }

    Help.getRandomInt = function(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }


})(window);